import React, {Component} from 'react';
import {connect} from "react-redux";
import './main.style.scss';
import {ListSites} from '../Sites/site.container'
import Header from "../Header/header.component";
import {createSelector} from "reselect";
import languages from "../../config/languages";
import LoadingBar from "../LoadingStatus/loading.component";
import {resetSite} from "../../redux/actions/siteAction";
import {resetWorkItem} from "../../redux/actions/workItemAction";
import {resetRoom} from "../../redux/actions/roomAction";

class MainComponent extends Component {

    componentDidMount() {
        this.props.dispatch(resetSite());
        this.props.dispatch(resetWorkItem());
        this.props.dispatch(resetRoom());
    }

    render() {
        const {sites} = this.props;
        const {language} = this.props;
        const {loading, error} = this.props.sites;
        return (
            <div className="AppComponent">
                <div className="container-fluid">
                    <Header title={languages[language].headerTitle}/>
                    <ListSites sites={sites}/>
                    <div className="col-12">
                        <LoadingBar loading={loading}/>
                        {
                            error != null ?
                                <div className="errorFetching">{error.message}</div>
                                :
                                ''
                        }
                    </div>
                </div>
            </div>
        );
    }
}

const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const siteSelector = createSelector(
    state => state.sites,
    sites => sites,
    loading => loading,
    error => error,
);

const mapStateToProps = createSelector(
    languageSelector,
    siteSelector,
    (language, sites) => ({
        language,
        sites
    })
);

export default connect(mapStateToProps)(MainComponent);
