import {
    GET_LANGUAGE,
    SET_LANGUAGE,
} from '../actions/languageAction';

export default function languageReducer(state = 'fi', action) {
    switch (action.type) {
        case GET_LANGUAGE:
            return {
                ...state,
            };
        case SET_LANGUAGE:
            return {
                ...state,
                language: action.payload,
            };
        default:
            return state;
    }
}