import React from 'react'
import '../ListItem/list.style.scss'
import ItemList from "../ListItem/item.component";
import {faCheckCircle} from "@fortawesome/free-solid-svg-icons";
import {WORKITEM_CALLBACK_ENUMS} from "./workitem_callbacks";

class WorkItemItem extends React.Component {

    constructor(props) {
        super(props);
        this.onItemSelectToggle = this.onItemSelectToggle.bind(this);
        // Not a best idea to have its own state for a particular component since we are using Redux for managing the state, but in cases like this should be fine.
        this.state = {itemSelected: false}
    }

    onItemSelectToggle(workItem) {
        this.setState({roomSelected: !this.state.roomSelected});
        this.props.callbackHandler(WORKITEM_CALLBACK_ENUMS.SELECT_WORKITEM, workItem);
    }

    render() {
        const props = this.props;

        return (
            <div className={'col-sm-12'} onClick={()=>{this.onItemSelectToggle(this.props.workItem)}}>
                <ItemList {...props} text={props.workItem.name} isItemSelected={this.state.roomSelected} badge={{data: '1.1.2019', status: 'success', icon: faCheckCircle}}/>
            </div>
        );
    }
}

export default WorkItemItem