import React from 'react'
import './list.style.scss'
import BadgeStatus from "../BadgetStatus/badget.component";

class ItemList extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        const props = this.props;
        const {isItemSelected} = this.props;

        return (
            <div className={isItemSelected ? 'row listItem itemSelected': 'row listItem'}>
                <div className={"col"}>
                    <span>{props.text}</span>
                </div>
                {
                    props.badge != null ?
                        <div className={"col text-right"}>
                            <BadgeStatus icon={props.badge.icon} data={props.badge.data}
                                         className={props.badge.status}/>
                        </div>
                        : ''
                }
            </div>
        );
    }
}

export default ItemList