import React from 'react';
import {Route, Switch} from 'react-router-dom'
import MainComponent from "./app/components/Main/main.component";
import WorkItemPageComponent from './app/components/WorkItem/workitem.page.component'
import RoomPageComponent from './app/components/Room/room.page.component'
import TaskDetailsComponent from './app/components/TaskDetails/task-details.component'
import ClearanceOptions from "./app/components/ClearanceOptions/clearance-options.component";

class Routes extends React.Component {
    render() {
        return (
            <Switch>
                <Route exact path="/" component={MainComponent}/>
                <Route exact path="/clearance-options" component={ClearanceOptions}/>
                <Route exact path="/work-items" component={WorkItemPageComponent}/>
                <Route path="/rooms" component={RoomPageComponent}/>
                <Route path="/task-details" component={TaskDetailsComponent}/>
            </Switch>
        )
    }
}

export default Routes;
