export const FETCH_ROOMS_BEGIN   = 'FETCH_ROOMS_BEGIN';
export const FETCH_ROOMS_SUCCESS = 'FETCH_ROOMS_SUCCESS';
export const FETCH_ROOMS_FAILURE = 'FETCH_ROOMS_FAILURE';
export const RESET_ROOMS = 'RESET_ROOMS';
export const SELECTED_ROOM = 'SELECTED_ROOM';
export const RESET_SELECTED_ROOMS = 'RESET_SELECTED_ROOMS';

export const fetchRoomsBegin = () => ({
    type: FETCH_ROOMS_BEGIN
});

export const fetchRoomsSuccess = rooms => ({
    type: FETCH_ROOMS_SUCCESS,
    payload: { rooms }
});

export const fetchRoomsFailure = error => ({
    type: FETCH_ROOMS_FAILURE,
    payload: { error }
});

export const selectedRoom = room => ({
    type: SELECTED_ROOM,
    payload: { room }
});

export const resetRoom = () => ({
    type: RESET_ROOMS
});

export const resetSelectedRooms = () => ({
    type: RESET_SELECTED_ROOMS
});
