import React from 'react';
import ReactDOM from 'react-dom';
import './app/assets/styles/index.scss';
import * as serviceWorker from './serviceWorker';
import thunk from 'redux-thunk';
import {applyMiddleware, combineReducers, createStore} from 'redux';
import siteReducer from './app/redux/reducers/siteReducer';
import workItemReducer from './app/redux/reducers/workItemReducer';
import roomReducer from './app/redux/reducers/roomReducer';
import languageReducer from './app/redux/reducers/languageReducer';
import {Provider} from 'react-redux';
import {BrowserRouter as Router} from 'react-router-dom'
import Routes from './routes';

const rootReducer = combineReducers({
    language: languageReducer,
    sites: siteReducer,
    workItems: workItemReducer,
    rooms: roomReducer,
});

const store = createStore(
    rootReducer,
    applyMiddleware(thunk),
);

ReactDOM.render(
    <Provider store={store}>
        <Router>
            <Routes/>
        </Router>
    </Provider>
    , document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
