import React from 'react';
import './workitem.style.scss';
import {connect} from "react-redux";
import workItemApi from "./workitem.api";
import {createSelector} from 'reselect';
import ListWorkitemsComponent from "./list.workitems.component";
import {fetchWorkItemsBegin, fetchWorkItemsFailure, fetchWorkItemsSuccess, selectedWorkItem, resetSelectedWorkItems} from "../../redux/actions/workItemAction";
import {WORKITEM_CALLBACK_ENUMS} from "./workitem_callbacks";

function withSubscription(DumpComponent) {
    return class Container extends React.Component {
        constructor(props) {
            super(props);
        }

        fetchAll = (page) => {
            this.props.dispatch(fetchWorkItemsBegin());
            workItemApi.getAll(page)
                .then(response => {
                    if (response.data) {
                        this.props.dispatch(fetchWorkItemsSuccess(response.data._embedded.companies));
                    } else {
                        this.props.dispatch(fetchWorkItemsFailure({message: "Fetching work items failed"}));
                    }
                });
        };

        onWorkItemSelect = (workItem) => {
            this.props.dispatch(selectedWorkItem(workItem));
        };

        resetSelectedWorkItems = () => {
            this.props.dispatch(resetSelectedWorkItems());
        };

        callbackHandler = (type, data) => {
            switch (type) {
                case WORKITEM_CALLBACK_ENUMS.FETCH_WORKITEMS:
                    this.fetchAll(2);
                    break;

                case WORKITEM_CALLBACK_ENUMS.SELECT_WORKITEM:
                    this.onWorkItemSelect(data);
                    break;

                case WORKITEM_CALLBACK_ENUMS.RESET_SELECTED_WORKITEM:
                    this.resetSelectedWorkItems();
                    break;


                default:
                    return false;
            }
        };

        render() {
            return <DumpComponent {...this.props} callbackHandler={this.callbackHandler}/>;
        }
    }
}

const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const mapStateToProps = createSelector(
    languageSelector,
    (language) => ({
        language,
    })
);

const ListWorkItemContainer = withSubscription(
    ListWorkitemsComponent
);

export const ListWorkItems = connect(mapStateToProps)(ListWorkItemContainer);
