import React, {Component} from 'react';
import {connect} from "react-redux";
import './workitem.style.scss';
import Header from "../Header/header.component";
import {ListWorkItems} from "./workitem.container";
import {createSelector} from "reselect";
import languages from "../../config/languages";
import {NavLink} from "react-router-dom";
import {Button} from "reactstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faArrowRight, faPlus, faSort} from "@fortawesome/free-solid-svg-icons";
import LoadingBar from "../LoadingStatus/loading.component";

class WorkItemPageComponent extends Component {

    componentDidMount() {

    }

    render() {
        console.log("WORKITEM PAGE ");
        console.log(this.props);

        const {workItems} = this.props;
        const { loading, error} = this.props.workItems;

        const {language} = this.props;

        return (
            <div className="WorkItemComponent">
                <div className="container-fluid">
                    <Header title={this.props.site.name}/>

                    <div className="row addExtraWorkHolder">
                        <div className={"col-12 mt-2 text-center"}>
                            <div className={"addExtraWork p-3"}>
                                <FontAwesomeIcon icon={faPlus}/>
                                <span className={'ml-3'}>{languages[language].addExtraWorkBtn}</span>
                            </div>
                        </div>
                        <div className={'col-12'}>
                            <div className={"row mt-3 jobHolder"}>
                                <div className={'col sectionTitle'}>
                                    {languages[language].job.toUpperCase()}
                                </div>
                                <div className={'col text-right'}>
                                    <FontAwesomeIcon icon={faSort}/>
                                </div>
                            </div>
                        </div>
                    </div>

                    <ListWorkItems workItems={workItems}/>

                    <div className={"row"}>
                        <NavLink to={"/rooms"} className={"bottomBtn"}>
                            <Button color="primary" disabled={false}>
                                {languages[language].next} <span className={"arrowRightIcon"}> <FontAwesomeIcon
                                className={"fas fa-xs"} icon={faArrowRight}/></span>
                            </Button>
                        </NavLink>
                    </div>

                    <div className={"row"}>
                        <div className="col">
                            <LoadingBar loading={loading}/>
                            {
                                error != null ?
                                    <div className="errorFetching">{error.message}</div>
                                    :
                                    ''
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const siteSelector = createSelector(
    state => state.sites.siteSelected,
    siteSelected => siteSelected,
);

const workItemSelector = createSelector(
    state => state.workItems,
    workItems => workItems,
    loading => loading,
    error => error,
);

const mapStateToProps = createSelector(
    languageSelector,
    siteSelector,
    workItemSelector,
    (language, site, workItems) => ({
        language,
        site,
        workItems
    })
);

export default connect(mapStateToProps)(WorkItemPageComponent);
