import {RESET_ROOMS} from "./roomAction";

export const FETCH_SITES_BEGIN   = 'FETCH_SITES_BEGIN';
export const FETCH_SITES_SUCCESS = 'FETCH_SITES_SUCCESS';
export const FETCH_SITES_FAILURE = 'FETCH_SITES_FAILURE';
export const RESET_SITES = 'RESET_SITES';
export const SELECTED_SITE = 'SELECTED_SITE';

export const fetchSitesBegin = () => ({
    type: FETCH_SITES_BEGIN
});

export const fetchSitesSuccess = sites => ({
    type: FETCH_SITES_SUCCESS,
    payload: { sites }
});

export const fetchSitesFailure = error => ({
    type: FETCH_SITES_FAILURE,
    payload: { error }
});

export const selectedSite = site => ({
    type: SELECTED_SITE,
    payload: { site }
});

export const resetSite = () => ({
    type: RESET_SITES
});

