import React from 'react';
import './task-details.style.scss';
import LoadingBar from "../LoadingStatus/loading.component";
import {Button, Form, FormGroup, Label, Input, FormFeedback, FormText, CustomInput} from 'reactstrap';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {
    faArrowRight,
    faCheckCircle,
    faSearch,
    faCalendar,
    faCalendarDay,
    faCalendarAlt
} from '@fortawesome/free-solid-svg-icons'
import {createSelector} from "reselect";
import {connect} from "react-redux";
import Header from "../Header/header.component";
import {Link} from "react-router-dom";
import languages from "../../config/languages";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import calendarIcon from '../../assets/images/ic-calendar.png';
import attachImageIcon from '../../assets/images/ic-attach.png';
import commentIcon from '../../assets/images/ic-comment.png';
import ItemListTxtLayout from "../ListItem/itemtxt.component";
import Modal from 'react-responsive-modal';
import FormValidator from "../../utils/validation";

class TaskDetailsComponent extends React.Component {

    constructor(props) {
        super(props);


        this.validator = new FormValidator([
            // {
            //     field: 'email',
            //     method: 'isEmpty',
            //     validWhen: false,
            //     message: 'Email is required.'
            // },
            // {
            //     field: 'email',
            //     method: 'isEmail',
            //     validWhen: true,
            //     message: 'That is not a valid email.'
            // },
            // {
            //     field: 'age',
            //     method: 'isInt',
            //     args: [{min: 21, max: 65}],  // an array of additional arguments
            //     validWhen: true,
            //     message: 'Your age must be an integer between 21 and 65'
            // },
            {
                field: 'roomComment',
                method: 'isEmpty',
                validWhen: false,
                message: 'Comment cannot be empty.',
            }
        ]);

        this.state = {
            status: null,
            shift: null,
            startDate: new Date(),
            open: false,
            room: null,
            roomComment: '',
            validation: this.validator.valid(),
        };

        this.handleStatusChange = this.handleStatusChange.bind(this);
        this.handleShiftChange = this.handleShiftChange.bind(this);
        this.handleCalendarChange = this.handleCalendarChange.bind(this);

        this.taskDetailsFormSubmitted = false;
    }

    componentDidMount() {

    }

    handleCalendarChange(date) {
        this.setState({
            startDate: date
        });
    }

    handleStatusChange(changeEvent) {
        this.setState({
            status: changeEvent.target.value
        });
    }

    handleShiftChange(changeEvent) {
        this.setState({
            shift: changeEvent.target.value
        });
    }

    onOpenRoomCommentModal = (no) => {
        this.setState({
            open: true,
            room: no
        });
    };

    onCloseRoomCommentModal = () => {
        this.setState({
            open: false,
            room: null,
            roomComment: ''
        });
    };

    handleInputChange = event => {
        event.preventDefault();

        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    handleTaskDetailsFormSubmit = event => {
        event.preventDefault();

        const validation = this.validator.validate(this.state);
        this.setState({validation});
        this.taskDetailsFormSubmitted = true;

        if (validation.isValid) {
            console.log("form valid")
        } else {
            console.log("form !valid")
        }
    };

    render() {
        const props = this.props;
        const {workItems, rooms} = this.props;
        const {language} = this.props;
        const {open} = this.state;

        let validation = this.taskDetailsFormSubmitted ?           // if the form has been taskDetailsFormSubmitted at least once
            this.validator.validate(this.state) :   // then check validity every time we render
            this.state.validation;                  // otherwise just use what's in state

        return (
            <div className="">
                <div className={"container-fluid"}>
                    <Header title={languages[language].taskDetails}/>
                </div>
                <div className="backgroundColor container-fluid">
                    <div className={"row"}>
                        <div
                            className={"col-12 sectionTitle pt-2"}>{languages[language].taskSelected.toUpperCase()} ({workItems.length})
                        </div>
                        {
                            workItems.map((task, index) => {
                                return (
                                    <div className={"col-12"}>
                                        <ItemListTxtLayout key={index} text={task.name}/>
                                    </div>
                                )
                            })
                        }
                    </div>

                    <div className={"row mt-3"}>
                        <div className={"col-12 mb-2 sectionTitle"}>
                            {languages[language].status.toUpperCase()}
                        </div>
                        <div className={"col"}>
                            <div className="radio">
                                <label>
                                    <input type="radio" value="option1"
                                           checked={this.state.status === 'option1'}
                                           onChange={this.handleStatusChange}/>
                                    <span className={"taskStatus"}>{languages[language].done}</span>
                                </label>
                            </div>
                        </div>
                        <div className={"col"}>
                            <div className="radio">
                                <label>
                                    <input type="radio" value="option2"
                                           checked={this.state.status === 'option2'}
                                           onChange={this.handleStatusChange}/>
                                    <span className={"taskStatus"}>{languages[language].checked}</span>
                                </label>
                            </div>
                        </div>
                        <div className={"col"}>
                            <div className="radio">
                                <label>
                                    <input type="radio" value="option3"
                                           checked={this.state.status === 'option3'}
                                           onChange={this.handleStatusChange}/>
                                    <span className={"taskStatus"}>{languages[language].skipped}</span>
                                </label>
                            </div>
                        </div>
                    </div>

                    <div className={"row mt-3"}>
                        <div className={"col-12 mb-2 sectionTitle"}>
                            {languages[language].date.toUpperCase()}
                        </div>
                        <div className={"col-12 mt-1"}>
                            <DatePicker
                                selected={this.state.startDate}
                                onChange={this.handleCalendarChange}
                            />
                            <span className={"arrowRightIcon"} style={{marginLeft: '10px',}}>
                                <img src={calendarIcon} alt=""/>
                            </span>
                        </div>
                    </div>

                    <div className={"row mt-3"}>
                        <div className={"col-12 mb-2 sectionTitle"}>
                            {languages[language].shift.toUpperCase()}
                        </div>
                        <div className={"col"}>
                            <div className="radio">
                                <label>
                                    <input type="radio" value="shift1" checked={this.state.shift === 'shift1'}
                                           onChange={this.handleShiftChange}/>
                                    <span className={"taskStatus"}>{languages[language].normal}</span>
                                </label>
                            </div>
                        </div>
                        <div className={"col"}>
                            <div className="radio">
                                <label>
                                    <input type="radio" value="shift2" checked={this.state.shift === 'shift2'}
                                           onChange={this.handleShiftChange}/>
                                    <span className={"taskStatus"}>{languages[language].evening}</span>
                                </label>
                            </div>
                        </div>
                        <div className={"col"}>
                            <div className="radio">
                                <label>
                                    <input type="radio" value="shift3" checked={this.state.shift === 'shift3'}
                                           onChange={this.handleShiftChange}/>
                                    <span className={"taskStatus"}>{languages[language].night}</span>
                                </label>
                            </div>
                        </div>
                    </div>

                    <div className={"row mt-3"}>
                        <div className={"col-12 mb-2"}>
                            <div className="form-check">
                                <input className="form-check-input" type="checkbox" value="" id="defaultCheck1"/>
                                <label className="form-check-label" htmlFor="defaultCheck1">
                                    <div className={"sectionTitle"}>
                                        {languages[language].totalTime.toUpperCase()}
                                    </div>
                                </label>
                            </div>
                        </div>
                        <div className={"col-5 mt-1"}>
                            <Input name="time" id="time" placeholder="Hour.min"/>
                        </div>
                        <div className={"col-7 mt-1"}>
                            Aika/huone: <br/>
                            4min/4 = 1min
                        </div>
                    </div>

                    <div className={"row mt-3 mb-5"}>
                        <div className={"col-12 mb-2 sectionTitle"}>
                            {languages[language].rooms.toUpperCase()} - {rooms.length} {languages[language].room} {languages[language].selected}
                        </div>
                        <div className={"col-12 mt-1"}>

                            {
                                rooms.map((room, index) => {
                                    return (
                                        <div className={"col-12"} key={index}>
                                            <div className="row taskDetailsRoom borderTopRadius mt-2">
                                                <div className="col-12 mini-box p-2">
                                                    <label htmlFor="" className={"roomNoMedium"}>{room.name} </label>
                                                    <label htmlFor="" className={"roomType ml-1"}> Toimisto</label>
                                                    <div className={"commentSectionContent"}>
                                                        Comment: lorem ipsum
                                                    </div>
                                                </div>
                                                <div
                                                    className="col mini-box roomActionHolder borderBottomRadius text-center">
                                                    <img src={attachImageIcon} alt=""/>
                                                    <label htmlFor=""
                                                           className={"imageUploadHolder"}>{languages[language].attachImage}</label>
                                                </div>
                                                <div
                                                    className="col mini-box roomActionHolder borderBottomRadius text-center borderLeft">

                                                    <div onClick={() => {
                                                        this.onOpenRoomCommentModal(room.name)
                                                    }}>
                                                        <img src={commentIcon} alt=""/><label htmlFor=""
                                                                                              className={"commentHolder"}>{languages[language].comment}</label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                            <Modal focusTrapped={true} open={open} onClose={this.onCloseRoomCommentModal} center>
                                <h2>Room: {this.state.room} </h2>
                                <div className={"writeComment"}>
                                    <div className={validation.roomComment.isInvalid ? 'has-error' : 'has-no-error'}>
                                        {
                                            this.taskDetailsFormSubmitted === true ?
                                                validation.roomComment.isInvalid === true ?
                                                    <span className="help-block">{validation.roomComment.message}</span>
                                                    :
                                                    ''
                                                :
                                                ''
                                        }
                                    </div>
                                    <div className={"row "}>
                                        <div className={"col-12 mt-2 modalHeader"}>
                                            <Label for="exampleText">Write your comment:</Label>
                                        </div>
                                        <div className={"col-12 mt-1 modalContent"}>
                                            <Input value={this.state.roomComment} onChange={this.handleInputChange}
                                                   type="textarea" name="roomComment" id="roomComment"/>
                                        </div>
                                        <div className={"col-12 modalFooter"}>
                                            <div className={"row mt-3"}>
                                                <div className={"col-6"}>
                                                    <button type="button" className="btn btn-secondary"
                                                            onClick={this.onCloseRoomCommentModal}>{languages[language].close}</button>
                                                </div>
                                                <div className={"col-6"}>
                                                    <button type="button" className="btn btn-secondary float-right"
                                                            onClick={this.handleTaskDetailsFormSubmit}>{languages[language].submit}</button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Modal>
                        </div>
                    </div>

                    <div className={"row"}>
                        <Link to={"/task-details"} className={"bottomBtn"} style={{padding: '0'}}>
                            <Button color="primary " disabled={false}>
                                {languages[props.language].finish}
                            </Button>
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}


const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const workItemSelector = createSelector(
    state => state.workItems.workItemsSelected,
    workItemsSelected => workItemsSelected,
);

const roomSelector = createSelector(
    state => state.rooms.roomsSelected,
    roomsSelected => roomsSelected,
);

const mapStateToProps = createSelector(
    languageSelector,
    workItemSelector,
    roomSelector,
    (language, workItems, rooms) => ({
        language,
        workItems,
        rooms,
    })
);

export default connect(mapStateToProps)(TaskDetailsComponent);
