import React from 'react';
import './site.style.scss';
import {connect} from "react-redux";
import sitesApi from "./sites.api";
import {createSelector} from 'reselect';
import ListSitesComponent from "./list.sites.component";
import AddSiteComponent from "./add.site.component";
import {fetchSitesBegin, fetchSitesFailure, fetchSitesSuccess, selectedSite} from "../../redux/actions/siteAction";
import {SITE_CALLBACK_ENUMS} from "./site_callbacks";

function withSubscription(DumpComponent) {
    return class Container extends React.Component {
        constructor(props) {
            super(props);
            this.fetchAll = this.fetchAll.bind(this);
        }

        fetchAll = (page) => {
            this.props.dispatch(fetchSitesBegin());
            sitesApi.getAll(page)
                .then(response => {
                    if (response.data) {
                        this.props.dispatch(fetchSitesSuccess(response.data._embedded.companies));
                    } else {
                        this.props.dispatch(fetchSitesFailure({message: "Fetching sites failed"}));
                    }
                });
        };

        onSiteSelect = (site) => {
            this.props.dispatch(selectedSite(site));
        };

        callbackHandler = (type, data) => {
            switch (type) {
                case SITE_CALLBACK_ENUMS.FETCH_SITES:
                    this.fetchAll(2);
                    break;

                case SITE_CALLBACK_ENUMS.SELECT_SITE:
                    this.onSiteSelect(data);
                    break;

                default:
                    return false;
            }
        };

        render() {
            return <DumpComponent {...this.props} callbackHandler={this.callbackHandler}/>;
        }
    }
}


const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const mapStateToProps = createSelector(
    languageSelector,
    (language) => ({
        language,
    })
);

const ListSiteContainer = withSubscription(
    ListSitesComponent
);

const AddSiteContainer = withSubscription(
    AddSiteComponent
);

export const ListSites = connect(mapStateToProps)(ListSiteContainer);
export const AddSites = connect(mapStateToProps)(AddSiteContainer);
