import React, {Component} from 'react';
import {connect} from "react-redux";
import './room.style.scss';
import Header from "../Header/header.component";
import {TabContent, TabPane, Nav, NavItem, NavLink, Button} from 'reactstrap';
import {createSelector} from "reselect";
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import languages from "../../config/languages";
import ListGroupItem from "reactstrap/es/ListGroupItem";
import ItemList from "../ListItem/item.component";
import {faArrowRight, faCheckCircle} from '@fortawesome/free-solid-svg-icons';
import {ListRooms} from "./room.container";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import LoadingBar from "../LoadingStatus/loading.component";
import {Link} from "react-router-dom";
import SearchField from 'react-search-field';


class RoomPageComponent extends Component {

    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            activeTab: '1',
        };
    }

    componentDidMount() {

    }

    searchByNumber(query){
        console.log(query);
    }

    floorChanged(val){
        console.log(val);
    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab
            });
        }
    }

    render() {
        const options = [
            'one', 'two', 'three'
        ];
        console.log("Room PAGE ");
        let props = this.props;
        const {language} = this.props;
        const {rooms} = this.props;
        const {loading, error} = this.props.rooms;

        console.log(this.props)
        return (
            <div className="RoomComponent">
                <div className="container-fluid">
                    <Header title={languages[props.language].rooms}/>
                </div>
                <div className={"roomTabs "}>
                    <Nav tabs>
                        <NavItem>
                            <NavLink
                                className={{active: this.state.activeTab === '1'}}
                                onClick={() => {
                                    this.toggle('1');
                                }}
                            >
                                {languages[props.language].rooms}
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                                className={{active: this.state.activeTab === '2'}}
                                onClick={() => {
                                    this.toggle('2');
                                }}
                            >
                                {languages[props.language].ground}
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                                className={{active: this.state.activeTab === '3'}}
                                onClick={() => {
                                    this.toggle('3');
                                }}
                            >
                                {languages[props.language].pictureStatus}
                            </NavLink>
                        </NavItem>
                    </Nav>
                    <TabContent activeTab={this.state.activeTab}>
                        <TabPane tabId="1" className={"backgroundColor"}>
                            <div className="container-fluid mb-3 pb-3">
                                <div className="row">
                                    <div className="col p-2 sectionTitle">
                                        {languages[props.language].taskSelected.toUpperCase()} ({this.props.workItems.length})
                                    </div>
                                </div>

                                <div className="row">
                                    <div className={"col-12"}>
                                        {
                                            this.props.workItems.map((work, index)=>{
                                                return (
                                                    <div className={'col-sm-12'}>
                                                        <ItemList  key={index} items={this.props.workItems} text={work.name} badge={{icon:faCheckCircle, data:'8.1.2019', status: 'success'}}/>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>

                                <ListRooms rooms={rooms}/>

                            </div>

                        </TabPane>
                        <TabPane tabId="2">
                            <div className="container-fluid backgroundColor">
                                <div className="row">
                                    <div className="col-12 p-2 pt-3">
                                        <SearchField style={{width:'100%'}}
                                            placeholder={languages[language].searchByNumber}
                                            onEnter={query => {this.searchByNumber(query)}}
                                        />
                                    </div>
                                    <div className="col-12 p-2">
                                        <div className={" floorFilter"}>
                                            {languages[props.language].rooms.toUpperCase()}
                                        </div>

                                        <div className={" "}>
                                            <Dropdown options={options} onChange={this.floorChanged} value={options} placeholder={"Select an option"} />
                                        </div>
                                    </div>
                                    <div className="col-12 p-2">
                                        room visualization
                                    </div>
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tabId="3">
                            3
                        </TabPane>
                    </TabContent>
                </div>

                <div className={"row"}>
                   <div className={"col"}>
                       <Link to={"/task-details"} className={"bottomBtn"} style={{padding:'0'}}>
                           <Button color="primary" disabled={false}>
                               {languages[props.language].next} <span className={"arrowRightIcon"}> <FontAwesomeIcon
                               className={"fas fa-xs"} icon={faArrowRight}/></span>
                           </Button>
                       </Link>
                   </div>
                </div>

                <div className={"row"}>
                    <div className="col">
                        <LoadingBar loading={loading}/>
                        {
                            error != null ?
                                <div className="errorFetching">{error.message}</div>
                                :
                                ''
                        }
                    </div>
                </div>
            </div>
        );
    }
}

const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const roomSelector = createSelector(
    state => state.rooms,
    rooms => rooms,
    loading => loading,
    error => error,
);

const workItemSelector = createSelector(
    state => state.workItems.workItemsSelected,
    workItemsSelected => workItemsSelected,
);

const mapStateToProps = createSelector(
    languageSelector,
    roomSelector,
    workItemSelector,
    (language, rooms, workItems) => ({
        language,
        rooms,
        workItems
    })
);

export default connect(mapStateToProps)(RoomPageComponent);
