import React from 'react';
import './room.style.scss';
import LoadingBar from "../LoadingStatus/loading.component";
import {Badge, Button} from 'reactstrap';
import HeaderList from "../ListItem/header.component";
import {faCheckCircle, faSearch} from '@fortawesome/free-solid-svg-icons'
import {NavLink} from "react-router-dom";
import languages from "../../config/languages";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faArrowRight, faSort, faPlus} from '@fortawesome/free-solid-svg-icons'
import {ROOM_CALLBACK_ENUMS} from './room_callbacks';
import RoomItemComponent from "./room.item.component";

class ListRoomsComponent extends React.Component {

    constructor(props){
        super(props)
    }

    componentDidMount() {
        if(!this.props.rooms.loaded){
            // this.props.callbackHandler(ROOM_CALLBACK_ENUMS.FETCH_ROOMS, '');
        }
        this.props.callbackHandler(ROOM_CALLBACK_ENUMS.RESET_SELECTED_ROOMS, '');
    }

    render() {
        const props = this.props;
        const {rooms} = this.props.rooms;

        return (
            <div>
                <div className={"row"}>
                    <HeaderList text={languages[props.language].floor}/>
                </div>
                <div className={"row ListRoomsComponent"}>
                    {
                        rooms.map((room, index)=>{
                            return (
                                <div key={index} className={'col-4'} style={{paddingLeft:'5px', paddingRight:'5px'}}>
                                    <RoomItemComponent {...props} room={room} />
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        );
    }
}

export default ListRoomsComponent
