import React from 'react'
import './room.style.scss'
import BadgeStatus from "../BadgetStatus/badget.component";
import {ROOM_CALLBACK_ENUMS} from './room_callbacks';

class RoomItemComponent extends React.Component {

    constructor(props) {
        super(props);
        this.onRoomSelectToggle = this.onRoomSelectToggle.bind(this);
        this.state = {roomSelected: false}
    }

    onRoomSelectToggle(room) {
        this.setState({roomSelected: !this.state.roomSelected});
        this.props.callbackHandler(ROOM_CALLBACK_ENUMS.SELECT_ROOM, room);
    }

    render() {
        const props = this.props;

        return (
            <div className={this.state.roomSelected ? 'col-sm-12 roomItem itemSelected': 'col-sm-12 roomItem '} onClick={()=>{this.onRoomSelectToggle(this.props.room)}}>
                <div className={"col-12 text-center"}>
                    <div className={"roomNoMedium"}>
                        <span>{props.room.name}</span>
                    </div>

                    <div className={"roomType"}>
                        <span>WC</span>
                    </div>
                </div>
                <div className={"col-12"}>
                    {
                        props.badge != null ?
                            <div className={"col text-center mt-1"}>
                                <BadgeStatus icon={props.badge.icon} data={props.badge.data}
                                             className={props.badge.status}/>
                            </div>
                            : ''
                    }
                </div>
            </div>
        );
    }
}

export default RoomItemComponent