import {
    FETCH_SITES_BEGIN,
    FETCH_SITES_SUCCESS,
    FETCH_SITES_FAILURE,
    SELECTED_SITE,
    RESET_SITES
} from '../actions/siteAction';

const initialState = {
    sites: [{"name":'site 1'}, {"name":'site 2'}, {"name":'site 3'}],
    siteSelected: {},
    loading: false,
    error: null,
    loaded: false,
};

export default function siteReducer(state = initialState, action) {
    switch(action.type) {
        case FETCH_SITES_BEGIN:
            return {
                ...state,
                loading: true,
                error: null
            };

        case FETCH_SITES_SUCCESS:
            return {
                ...state,
                loading: false,
                loaded: true,
                sites: action.payload.sites
            };

        case FETCH_SITES_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload.error,
                sites: []
            };

        case SELECTED_SITE:
            return {
                ...state,
                siteSelected: action.payload.site
            };

        case RESET_SITES:
            return {
                ...state,
                loaded: false,
            };

        default:
            return state;
    }
}