import React from 'react';
import './workitem.style.scss';
import LoadingBar from "../LoadingStatus/loading.component";
import {Badge, Button} from 'reactstrap';
import HeaderList from "../ListItem/header.component";
import {faCheckCircle, faSearch} from '@fortawesome/free-solid-svg-icons'
import {NavLink} from "react-router-dom";
import languages from "../../config/languages";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'

import {faArrowRight, faSort, faPlus} from '@fortawesome/free-solid-svg-icons'
import WorkItemItem from "./workitem-item.component";
import {WORKITEM_CALLBACK_ENUMS} from "./workitem_callbacks";

class ListWorkitemsComponent extends React.Component {

    constructor(props){
        super(props)
    }

    componentDidMount() {
        if(!this.props.workItems.loaded){
            // this.props.callbackHandler(WORKITEM_CALLBACK_ENUMS.FETCH_WORKITEMS, '');
        }
        this.props.callbackHandler(WORKITEM_CALLBACK_ENUMS.RESET_SELECTED_WORKITEM, '');
    }

    render() {
        const props = this.props;
        const {workItems} = this.props.workItems;
        return (
            <div className="row ListWorkitemsComponent">
                <HeaderList text={'1xx ' + languages[props.language].frequency}/>
                <div className={"col-12"}>
                    {
                        workItems.map((workItem, index) =>
                            <WorkItemItem {...props} key={index} workItem={workItem}/>
                        )
                    }
                </div>
            </div>
        );
    }
}

export default ListWorkitemsComponent
