import React from 'react';
import './room.style.scss';
import {connect} from "react-redux";
import roomApi from "./room.api";
import {createSelector} from 'reselect';
import ListRoomsComponent from "./list.rooms.component";
import {fetchRoomsBegin, fetchRoomsFailure, fetchRoomsSuccess, selectedRoom, resetSelectedRooms} from "../../redux/actions/roomAction";
import {ROOM_CALLBACK_ENUMS} from './room_callbacks'

function withSubscription(DumpComponent) {
    return class Container extends React.Component {
        constructor(props) {
            super(props);
        }

        fetchAll = (page) => {
            this.props.dispatch(fetchRoomsBegin());
            roomApi.getAll(page)
                .then(response => {
                    if (response.data) {
                        this.props.dispatch(fetchRoomsSuccess(response.data._embedded.companies));
                    } else {
                        this.props.dispatch(fetchRoomsFailure({message: "Fetching rooms failed"}));
                    }
                });
        };

        onRoomSelect = (room) => {
            this.props.dispatch(selectedRoom(room));
        };

        resetSelectedRooms = () => {
            this.props.dispatch(resetSelectedRooms());
        };

        callbackHandler = (type, data) => {
            switch (type) {
                case ROOM_CALLBACK_ENUMS.FETCH_ROOMS:
                    this.fetchAll(2);
                    break;

                case ROOM_CALLBACK_ENUMS.SELECT_ROOM:
                    this.onRoomSelect(data);
                    break;

                case ROOM_CALLBACK_ENUMS.RESET_SELECTED_ROOMS:
                    this.resetSelectedRooms();
                    break;

                default:
                    return false;
            }
        };

        render() {
            return <DumpComponent {...this.props}  callbackHandler={this.callbackHandler}/>;
        }
    }
}

const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const mapStateToProps = createSelector(
    languageSelector,
    (language) => ({
        language,
    })
);

const ListRoomContainer = withSubscription(
    ListRoomsComponent
);

export const ListRooms = connect(mapStateToProps)(ListRoomContainer);
