import React from 'react'
import logo from '../../assets/images/logo@2x.png'
import './clearance-options.style.scss'
import languages from "../../config/languages";
import {createSelector} from "reselect";
import {connect} from "react-redux";

const ClearanceOptions = (props) => (

    <div className={"clearanceOptions backgroundColor pl-3 pr-3 "}>
        <div className={"text-center logo mb-3"}>
            <img src={logo} alt=""/>
        </div>
        <div className={"primaryOptions listItem"}>
            <div className="row align-items-center h-100">
                <div className="mx-auto">
                    {languages[props.language].maintenance}
                </div>
            </div>
        </div>
        <div className={"primaryOptions listItem text-center"}>
            <a href="./work-items">
                <div className="row align-items-center h-100">
                    <div className="mx-auto text-center">
                        {languages[props.language].infrequent}
                    </div>
                </div>
            </a>

        </div>
        <div className={"primaryOptions listItem text-center "}>
            <div className="row align-items-center h-100">
                <div className="mx-auto text-center">
                    {languages[props.language].addExtraWorkBtn}
                </div>
            </div>
        </div>
        <div className={"secondaryOptions listItem"}>
            <div className="row align-items-center h-100">
                <div className="mx-auto text-center">
                    {languages[props.language].message} (1)
                </div>
            </div>
        </div>
        <div className={"secondaryOptions viewDocument"}>
            <div className="row align-items-center h-100">
                <div className="mx-auto text-center">
                    Lorem ipsum
                </div>
            </div>
        </div>
    </div>
);

const languageSelector = createSelector(
    state => state.language,
    language => language,
);

const mapStateToProps = createSelector(
    languageSelector,
    (language) => ({
        language
    })
);

export default connect(mapStateToProps)(ClearanceOptions);
