import React from 'react'
import './list.style.scss'

class ItemListTxtLayout extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        const props = this.props;

        return (
            <div className={"listItemTxtLayout"}>
                <span>{props.text}</span>
            </div>
        );
    }
}

export default ItemListTxtLayout