import {
    FETCH_ROOMS_BEGIN,
    FETCH_ROOMS_SUCCESS,
    FETCH_ROOMS_FAILURE,
    SELECTED_ROOM,
    RESET_ROOMS,
    RESET_SELECTED_ROOMS,
} from '../actions/roomAction';

import {containsObject, remove} from '../../utils/utils';

const initialState = {
    rooms: [{"name":'101'}, {"name":'201'}, {"name":'300'}, {"name":'305'}, {"name":'400'}, {"name":'500'}],
    roomsSelected: [],
    loading: false,
    error: null,
    loaded: false,
};

export default function roomReducer(state = initialState, action) {
    switch(action.type) {
        case FETCH_ROOMS_BEGIN:
            return {
                ...state,
                loading: true,
                error: null
            };

        case FETCH_ROOMS_SUCCESS:
            return {
                ...state,
                loading: false,
                loaded: true,
                rooms: action.payload.rooms
            };

        case FETCH_ROOMS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload.error,
                rooms: []
            };

        case SELECTED_ROOM:
            let arr = [];

            if (containsObject(action.payload.room, state.roomsSelected)) {
               arr = remove(state.roomsSelected, action.payload.room);
            } else {
               arr = [...state.roomsSelected, action.payload.room];
            }

            return {
                ...state,
                roomsSelected: arr
            };

        case RESET_ROOMS:
            return {
                ...state,
                loaded: false,
            };

        case RESET_SELECTED_ROOMS:
            return {
                ...state,
                roomsSelected: [],
            };

        default:
            return state;
    }
}