import React from 'react';
import './site.style.scss';

class AddSiteComponent extends React.Component {

    componentDidMount() {

    }

    render() {
        const {sites, loading, error} = this.props.sites;
        return (
            <div className="AddSiteComponent">
                <h2>AddSiteComponent</h2>
                {
                    sites.map((item, index) =>
                        <li key={index}>{item.name}</li>
                    )
                }
            </div>
        );
    }
}

export default AddSiteComponent