import React from 'react';
import './site.style.scss';
import LoadingBar from "../LoadingStatus/loading.component";
import {  Button } from 'reactstrap';
import ItemList from "../ListItem/item.component";
import HeaderList from "../ListItem/header.component";
import BadgeStatus from "../BadgetStatus/badget.component";

import { faCheckCircle, faSearch } from '@fortawesome/free-solid-svg-icons'
import {NavLink} from "react-router-dom";
import {SITE_CALLBACK_ENUMS} from "./site_callbacks";

class ListSitesComponent extends React.Component {

    componentDidMount() {
        if(!this.props.sites.loaded){
            // this.props.callbackHandler(SITE_CALLBACK_ENUMS.FETCH_SITES, '');
        }
    }

    render() {
        const props = this.props;
        const {sites} = this.props.sites;

        return (
            <div className="row ListSiteComponent">
                {/*<Button color="danger" onClick={() => this.props.fetchAll(2)}>Load Products 2</Button>*/}
                <HeaderList text={"client 1"}/>
                <div className={"col-12"}>
                    {
                        sites.map((site, index) =>
                            <NavLink key={index} to={"/clearance-options"} onClick={() => this.props.callbackHandler(SITE_CALLBACK_ENUMS.SELECT_SITE, site)}>
                                <div className={'col-sm-12'} >
                                    <ItemList  key={index} {...props}  text={site.name}/>
                                </div>
                            </NavLink>
                        )
                    }
                </div>
            </div>
        );
    }
}

export default ListSitesComponent
