import React from 'react';
import './loading.style.scss';

const LoadingBar = (props) => {
    const {loading} = props;
    return (
        <div className="LoadingBar">
            {
                loading === true ?
                    <div className="loading">Loading&#8230;</div>
                    :
                    ''
            }
        </div>
    )
};

export default LoadingBar
